import { addShop } from '@/request/API/index.js';
import { mapState } from 'vuex';
export default {
	data() {
		return {
			ishow: true,
			img1: [], //身份证正面
			img2: [], //身份证反面
			img3: [], //店铺logo
			img4: [], //微信收款码
			img5: [], //支付宝收款码
			img6: [], //营业执照
			phone: '', //手机号
			weChatId: '', //微信号
			idCard: '', //身份证号
			idFront: '', //身份证正面照
			idBack: '', //身份证反面照
			shopName: '', //店铺名称
			shopDesc: '', //店铺简介
			shopAddress: '', //店铺地址
			shopLogo: '', //店铺logo
			shopLicence: '', //店铺营业执照
			weChatPhoto: '', //微信收款二维码
			aliPayPhoto: '' //支付宝收款二维码
		};
	},
	onLoad() {},
	computed: {
		...mapState(['userId'])
	},
	methods: {
		//身份证正面
		paizhao1() {
			uni.chooseImage({
				count: 1, //默认9
				sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
				sourceType: ['album', 'camera'], //从相册选择
				success: res => {
					this.img1 = res.tempFilePaths;
					//上传图片
					uni.uploadFile({
						url: baseURL + '/file/upload',
						filePath: res.tempFilePaths[0],
						name: 'file',
						formData: {
							user: 'test',
							file: res.tempFilePaths[0]
						},
						success: uploadFileRes => {
							let aaa = JSON.parse(uploadFileRes.data);
							if (aaa.code == 0) {
								console.log(uploadFileRes);
								this.idFront = aaa.data;
								uni.showToast({
									title: '上传成功'
								});
							}
						}
					});
				}
			});
		},
		//身份证反面
		paizhao2() {
			uni.chooseImage({
				count: 1, //默认9
				sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
				sourceType: ['album', 'camera'], //从相册选择
				success: res => {
					this.img2 = res.tempFilePaths;
					uni.uploadFile({
						url: baseURL + '/file/upload',
						filePath: res.tempFilePaths[0],
						name: 'file',
						formData: {
							user: 'test',
							file: res.tempFilePaths[0]
						},
						success: uploadFileRes => {
							let aaa = JSON.parse(uploadFileRes.data);
							if (aaa.code == 0) {
								console.log(uploadFileRes);
								this.idBack = aaa.data;
								uni.showToast({
									title: '上传成功'
								});
							}
						}
					});
				}
			});
		},
		//logo
		paizhao3() {
			uni.chooseImage({
				count: 1, //默认9
				sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
				sourceType: ['album', 'camera'], //从相册选择
				success: res => {
					this.img3 = res.tempFilePaths;
					uni.uploadFile({
						url: baseURL + '/file/upload',
						filePath: res.tempFilePaths[0],
						name: 'file',
						formData: {
							user: 'test',
							file: res.tempFilePaths[0]
						},
						success: uploadFileRes => {
							let aaa = JSON.parse(uploadFileRes.data);
							if (aaa.code == 0) {
								console.log(uploadFileRes);
								this.shopLogo = aaa.data;
								uni.showToast({
									title: '上传成功'
								});
							}
						}
					});
				}
			});
		},
		//微信收款码
		paizhao4() {
			uni.chooseImage({
				count: 1, //默认9
				sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
				sourceType: ['album', 'camera'], //从相册选择
				success: res => {
					this.img4 = res.tempFilePaths;
					uni.uploadFile({
						url: baseURL + '/file/upload',
						filePath: res.tempFilePaths[0],
						name: 'file',
						formData: {
							user: 'test',
							file: res.tempFilePaths[0]
						},
						success: uploadFileRes => {
							let aaa = JSON.parse(uploadFileRes.data);
							if (aaa.code == 0) {
								console.log(uploadFileRes);
								this.weChatPhoto = aaa.data;
								uni.showToast({
									title: '上传成功'
								});
							}
						}
					});
				}
			});
		},
		//支付宝收款码
		paizhao5() {
			uni.chooseImage({
				count: 1, //默认9
				sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
				sourceType: ['album', 'camera'], //从相册选择
				success: res => {
					this.img5 = res.tempFilePaths;
					uni.uploadFile({
						url: baseURL + '/file/upload',
						filePath: res.tempFilePaths[0],
						name: 'file',
						formData: {
							user: 'test',
							file: res.tempFilePaths[0]
						},
						success: uploadFileRes => {
							let aaa = JSON.parse(uploadFileRes.data);
							if (aaa.code == 0) {
								console.log(uploadFileRes);
								this.aliPayPhoto = aaa.data;
								uni.showToast({
									title: '上传成功'
								});
							}
						}
					});
				}
			});
		},
		//营业执照
		paizhao6() {
			uni.chooseImage({
				count: 1, //默认9
				sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
				sourceType: ['album', 'camera'], //从相册选择
				success: res => {
					this.img6 = res.tempFilePaths;
					uni.uploadFile({
						url: baseURL + '/file/upload',
						filePath: res.tempFilePaths[0],
						name: 'file',
						formData: {
							user: 'test',
							file: res.tempFilePaths[0]
						},
						success: uploadFileRes => {
							let aaa = JSON.parse(uploadFileRes.data);
							if (aaa.code == 0) {
								console.log(uploadFileRes);
								this.shopLicence = aaa.data;
								uni.showToast({
									title: '上传成功'
								});
							}
						}
					});
				}
			});
		},
		//入驻
		ruzhu() {
			if (this.phone == '') {
				uni.showModal({
					title: '',
					content: '请输入手机号',
					showCancel: false
				});
				return;
			}
			if (this.weChatId == '') {
				uni.showModal({
					title: '',
					content: '请输入微信号',
					showCancel: false
				});
				return;
			}
			if (this.idCard == '') {
				uni.showModal({
					title: '',
					content: '请输入身份证号',
					showCancel: false
				});
				return;
			}
			const reg2 = /(^\d{15}$)|(^\d{18}$)|(^\d{17}(\d|X|x)$)/;
			if (!reg2.test(this.idCard)) {
				uni.showModal({
					title: '',
					content: '身份证号输入有误',
					showCancel: false
				});
				return;
			}
			if (this.idFront == '') {
				uni.showModal({
					title: '',
					content: '请上传身份证正面照',
					showCancel: false
				});
				return;
			}
			if (this.idBack == '') {
				uni.showModal({
					title: '',
					content: '请上传身份证反面照',
					showCancel: false
				});
				return;
			}
			if (this.shopName == '') {
				uni.showModal({
					title: '',
					content: '请输入店铺名称',
					showCancel: false
				});
				return;
			}
			if (this.shopDesc == '') {
				uni.showModal({
					title: '',
					content: '请输入店铺简介',
					showCancel: false
				});
				return;
			}
			if (this.shopAddress == '') {
				uni.showModal({
					title: '',
					content: '请输入店铺地址',
					showCancel: false
				});
				return;
			}
			if (this.shopLogo == '') {
				uni.showModal({
					title: '',
					content: '请上传店铺图片',
					showCancel: false
				});
				return;
			}
			if (this.shopLicence == '') {
				uni.showModal({
					title: '',
					content: '请上传店铺营业执照',
					showCancel: false
				});
				return;
			}
			if (this.weChatPhoto == '') {
				uni.showModal({
					title: '',
					content: '请上传微信收款二维码',
					showCancel: false
				});
				return;
			}
			if (this.aliPayPhoto == '') {
				uni.showModal({
					title: '',
					content: '请上传支付宝收款二维码',
					showCancel: false
				});
				return;
			}
			const reg = /^[1][3,4,5,7,8][0-9]{9}$/;
			if (!reg.test(this.phone)) {
				console.log(this.phone);
				uni.showModal({
					title: '',
					content: '请输入正确的手机号码',
					showCancel: false
				});
				return;
			}
			let params = {
				userId: this.userId,
				phone: this.phone, //手机号
				weChatId: this.weChatId, //微信号
				idCard: this.idCard, //身份证号
				idFront: this.idFront, //身份证正面照
				idBack: this.idBack, //身份证反面照
				shopName: this.shopName, //店铺名称
				shopDesc: this.shopDesc, //店铺简介
				shopAddress: this.shopAddress, //店铺地址
				shopLogo: this.shopLogo, //店铺logo
				shopLicence: this.shopLicence, //店铺营业执照
				weChatPhoto: this.weChatPhoto, //微信收款二维码
				aliPayPhoto: this.aliPayPhoto //支付宝收款二维码
			};
			this.ishow = false;
			addShop(params).then(res => {
				if (res.data.code == 0) {
					uni.showToast({
						title: '申请成功',
						icon: 'none',
						duration: 1000
					});
					setTimeout(res => {
						uni.navigateBack({
							data: 1
						});
					}, 1500);
				} else {
					uni.showToast({
						title: '申请失败',
						icon: 'none',
						duration: 1000
					});
				}
			});
		}
	}
};
